import React from "react";
import { useForm } from "react-hook-form";
import { cn } from "../../../utils/custom_functions";
import { FaCheck } from "react-icons/fa";

export const RouteDetailForm = React.forwardRef(
  (
    {
      className,
      route,
      routeIndex,
      updateRoute,
      toggleRouteDetailVisibility,
      disableUserInterface,
      ...props
    },
    ref
  ) => {
    const { register, handleSubmit, reset } = useForm();
    const onSubmit = (data) => {
      updateRoute(routeIndex, { ...route, ...data });
      toggleRouteDetailVisibility();
      reset();
    };
    return (
      <form
        {...props}
        ref={ref}
        className={cn("w-full hidden", className)}
        onSubmit={handleSubmit(onSubmit)}
      >
        <div>
          <input
            {...register("route_detail", {
              disabled: disableUserInterface,
              // required: "Please enter not youd like to convey",
            })}
            type="text"
            name="route_detail"
            placeholder="Please enter details"
            className={cn(
              "text-sm text-slate-800 w-full relative shadow-sm block rounded-md border-0 border-b-gray-200 focus:z-10 ring-0 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none"
            )}
          />
        </div>
        <div className="w-full flex flex-row justify-end items-center px-2 pt-2">
          <button
            type="submit"
            className="text-green-700 hover:text-opacity-75 disabled:pointer-events-none disabled:opacity-75"
          >
            <FaCheck />
          </button>
        </div>
      </form>
    );
  }
);
