import { useEffect, useRef, useState } from "react";
import { cn } from "../../../utils/custom_functions";
import { IoLocationSharp } from "react-icons/io5";

export function SearchRouteInputField({
  disableUserInterface,
  useRoutesOutput,
  setDisableUserInterface,
  ...props
}) {
  const inputRef = useRef();
  const { data, status, handleRouteSelect, search } = useRoutesOutput;
  const searchedMenuNavbar = useRef(null);
  const toggleSearchMenuVisibility = (visibility) => {
    if (visibility) {
      searchedMenuNavbar.current.classList.remove("hidden");
      return;
    }
    // visible
    searchedMenuNavbar.current.classList.add("hidden");
  };

  useEffect(() => {
    if (status === "OK") toggleSearchMenuVisibility(true);
    else toggleSearchMenuVisibility(false);
  }, [status]);

  return (
    <div className="mt-4">
      <div className="relative">
        <input
          ref={inputRef}
          {...props}
          disabled={disableUserInterface}
          type="text"
          placeholder="Search location"
          onChange={(e) => {
            search(e.currentTarget.value);
          }}
          className={cn(
            "text-sm text-slate-800 w-full shadow-sm block rounded-md border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
            {}
          )}
        />
        <div ref={searchedMenuNavbar} className="hidden">
          <ul className="w-full rounded-md bg-white shadow-md border border-gray-200rounded-md absolute top-12 flex flex-col justify-center items-start">
            {data?.map((value, index) => {
              return (
                <li
                  key={index}
                  className="border-b border-gray-200 px-2 py-2 bg-gray-100 flex w-full gap-1 cursor-pointer"
                  onClick={handleRouteSelect(
                    value,
                    inputRef,
                    setDisableUserInterface
                  )}
                >
                  <div>
                    <IoLocationSharp size={15} className="text-gray-400" />
                  </div>
                  <p className="text-xs text-black font-bold">
                    {value.structured_formatting.main_text},{" "}
                    <span className="font-normal">
                      {value.structured_formatting.secondary_text}
                    </span>
                  </p>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    </div>
  );
}
