import { useMutation } from "react-query";
import { computeDistanceFunction } from "../../api/api";

export function useComputeDistance(callback) {
  const {
    data,
    isLoading: isComputeDistanceLoading,
    isSuccess: isComputeDistanceSuccess,
    mutateAsync: computeDistanceAsync,
    isError: isComputeDistanceError,
    error: computeDistanceError,
  } = useMutation({
    mutationKey: "compute-distance",
    mutationFn: computeDistanceFunction,
    onMutate: (_) => {
      callback((r) => true);
    },
    onSuccess: (_) => {
      callback((r) => false);
    },
    onError: (_) => {
      callback((r) => false);
    },
  });

  return {
    data,
    isComputeDistanceLoading,
    isComputeDistanceSuccess,
    isComputeDistanceError,
    computeDistanceError,
    computeDistanceAsync,
  };
}
