import { RouteCard } from "./route_card";

export function DisplayRoutes({ useArrayOutput, disableUserInterface }) {
  const { array, update, remove } = useArrayOutput;

  return (
    <div>
      {array.map((route, i) => {
        return (
          <RouteCard
            className="mb-1"
            key={i}
            routeIndex={i}
            route={route}
            updateRoute={update}
            removeRoute={remove}
            disableUserInterface={disableUserInterface}
          />
        );
      })}
    </div>
  );
}
