import usePlacesAutocomplete, {
  getGeocode,
  getLatLng,
} from "use-places-autocomplete";

export function useRoutesHook(push) {
  const {
    value,
    setValue: search,
    clearSuggestions,
    suggestions: { data, status },
  } = usePlacesAutocomplete({
    callbackName: "onInit",
    requestOptions: { componentRestrictions: { country: "za" } },
    debounce: 100,
  });

  const handleRouteSelect =
    ({ description, structured_formatting }, ref, disableUserInterface) =>
    async () => {
      disableUserInterface((r) => true);
      try {
        search(description, false);
        clearSuggestions();
        await getGeocode({ address: description }).then((results) => {
          const { main_text, secondary_text } = structured_formatting;
          const { lat, lng } = getLatLng(results[0]);
          push({
            purpose: "Pick up or Drop off",
            route_detail: "",
            primary_text: main_text,
            secondary_text: secondary_text,
            lat: lat,
            lng: lng,
          });
          if (ref) ref.current.value = "";
          disableUserInterface((r) => false);
        });
      } catch (error) {}
    };

  // returns
  return { data, status, handleRouteSelect, search };
}
